import { init } from '@lpx/core';
import { ContextMenu, createContextMenuWithin } from './context-menu';

const TOOLBAR_SELECTOR = '#lpx-toolbar';

export class Toolbar {
  contextMenu: ContextMenu;

  static readonly contextMenuName = 'user-context-menu';

  private constructor(private el: Element) {}

  static create(el: Element) {
    return new Toolbar(el).withContextMenu();
  }

  withContextMenu() {
    this.contextMenu = createContextMenuWithin(this.el, Toolbar.contextMenuName);
    return this;
  }
}

init.push(() => {
  document.querySelectorAll(TOOLBAR_SELECTOR).forEach(Toolbar.create);
});
