export interface LeptonXGlobalConfig {
  eventPrefix: string;
  cookiePrefix: string;
  localStoragePrefix: string;

  defaultSettings?: { [key: string]: string };
}

const defaults: LeptonXGlobalConfig = {
  eventPrefix: 'lpx',
  cookiePrefix: 'lpx',
  localStoragePrefix: 'lpx',
};

var leptonXConfig: LeptonXGlobalConfig = globalThis.leptonXConfig || ({} as LeptonXGlobalConfig);

export const globalConfig: LeptonXGlobalConfig = {
  ...defaults,
  ...leptonXConfig,
};

export function withEventPrefix(eventName: string) {
  return `${globalConfig.eventPrefix}:${eventName}`;
}

export function withCookiePrefix(cookieName: string) {
  return `${globalConfig.cookiePrefix}_${cookieName}`;
}

export function withLocalStoragePrefix(storageKey: string) {
  return `${globalConfig.localStoragePrefix}:${storageKey}`;
}

export function addDefaultSettings(settings: { [key: string]: string }) {
  globalConfig.defaultSettings = {
    ...settings,
    ...globalConfig.defaultSettings,
  };
}
