import { MenuItem } from '@lpx/core';

const SELECTOR = '[data-lpx-toggle="sidebar"]';

export class SideMenuToggle {
  private constructor(private el: Element, private sideMenu: BaseSideMenu) {}

  static create(sideMenu: BaseSideMenu) {
    const el = sideMenu.el.querySelector(SELECTOR);
    return new SideMenuToggle(el, sideMenu);
  }

  withClickListener(handler: (event: Event) => void) {
    if (this.el) {
      this.el.addEventListener('click', handler);
    }
    return this;
  }
}

export type ElementOrNull = Element | null | undefined;

const LAYOUT_ID = '#lpx-wrapper';
const HOVER_TRIGGER_CLASS = 'hover-trigger';

export class BaseSideMenu {
  menuItems: MenuItem[] = [];

  sideMenuToggle: SideMenuToggle;

  protected constructor(protected _el: Element) {
    this.createMenuItems();
    this.highlightCurrentPage();
  }

  get el() {
    return this._el;
  }

  static create() {
    return new BaseSideMenu(document.querySelector('#lpx-sidebar')).withToggle();
  }

  withToggle() {
    this.sideMenuToggle = SideMenuToggle.create(this).withClickListener(this.toggleSidebarHover.bind(this));
    return this;
  }

  highlightCurrentPage() {
    const compareFn = (menuItem: MenuItem) => menuItem.el.getAttribute('href') === location.pathname.replace(/\/$/, '');
    this.menuItems.forEach((menuItem: MenuItem) => menuItem.setSelectedRecursive(compareFn));
  }

  toggleSidebarHover() {
    const layout = document.querySelector(LAYOUT_ID);
    if (layout) {
      layout.classList.toggle(HOVER_TRIGGER_CLASS);
    }
  }

  createMenuItems() {
    const menuItems = this.el.querySelectorAll(`.outer-menu-item > .lpx-menu-item`);
    menuItems.forEach(item => {
      const menuItem = new MenuItem(item);
      this.menuItems.push(menuItem);
    });
  }
}
