import {
  EventHandler,
  globalConfig,
  MenuItem,
  readFromCookieOrLocalStorage,
  setCookieAndLocalStorage,
  SettingEvent,
} from '@lpx/core';

export abstract class SettingGroup {
  protected menuItem: MenuItem;

  protected constructor(public readonly name: string, protected eventType: new (data) => SettingEvent<any>) {}

  protected withPersistedValue() {
    const defaultValue = readFromCookieOrLocalStorage(this.name) || globalConfig.defaultSettings[this.name];
    this.setInitialDefaultValue(defaultValue);
    this.updateAllSelected(defaultValue);

    return this;
  }

  abstract setInitialDefaultValue(defaultValue: string): void;

  createSettingGroupWithMenu(el: Element) {
    this.menuItem = MenuItem.create(el).withEventHandler(this.onMenuItemClick.bind(this));
    return this;
  }

  onMenuItemClick(event: CustomEvent<MenuItem>) {
    const detail = event.detail.el as HTMLElement;
    const selectedSetting = detail.dataset.lpxSetting;
    const settingEvent = SettingEvent.create(this.eventType, selectedSetting);
    EventHandler.dispatch(event.target as Element, settingEvent);
    this.updateSelected(selectedSetting);
    this.persistSelected(selectedSetting);
  }

  persistSelected(setting: string) {
    setCookieAndLocalStorage(this.name, setting);
  }

  abstract updateSelected(selectedSetting: string): void;

  updateSelectedMenuItem(selectedSetting: string) {
    this.menuItem.selectChild(child => child.el.dataset.lpxSetting === selectedSetting);
  }

  updateAllSelected(selectedString: string): void {
    this.updateSelected(selectedString);
    this.updateSelectedMenuItem(selectedString);
  }

  collapse() {
    this.menuItem.setInnerMenuCollapsed();
  }

  expand() {
    this.menuItem.setInnerMenuExpanded();
  }
}
