import { EventHandler, init, MenuItem } from '@lpx/core';
import { BaseSideMenu } from '@lpx/layouts/common/side-menu';

class SideMenuFilter {
  input: HTMLInputElement;
  clearBtn?: HTMLElement;

  filterHandler: (filterValue: string) => void;

  private constructor(private el: Element) {}

  static create(el: Element, handler: (filterValue: string) => void) {
    return new SideMenuFilter(el).withInputListener(handler).withInputClearListener();
  }

  withInputListener(handler: (filterValue: string) => void) {
    this.filterHandler = handler;
    this.input = this.el.querySelector<HTMLInputElement>('.menu-filter-input');
    EventHandler.on(this.input, 'keyup', this.onInput.bind(this));
    return this;
  }

  withInputClearListener() {
    this.clearBtn = this.el.querySelector('.menu-filter-clear');
    EventHandler.on(this.clearBtn, 'click', this.clear.bind(this));
    return this;
  }

  onInput() {
    const filterValue = this.input.value;
    if (filterValue) {
      this.clearBtn?.classList.remove('hidden');
    } else {
      this.clear();
    }
    this.filterHandler(filterValue);
  }

  clear() {
    this.input.value = '';
    this.clearBtn?.classList.add('hidden');
    this.filterHandler('');
  }
}

class SideMenu extends BaseSideMenu {
  filter: SideMenuFilter;

  static create() {
    return new SideMenu(document.querySelector('#lpx-sidebar')).withToggle().withMenuFilter();
  }

  withMenuFilter() {
    this.filter = SideMenuFilter.create(this.el, this.onFilter.bind(this));
    return this;
  }

  onFilter(filterValue: string) {
    const fuzzySearch = (text: string) => {
      const pattern = '.*' + filterValue.replace(/\s/g, '').split('').join('.*') + '.*';

      const regex = new RegExp(pattern, 'i');
      return regex.test(text);
    };

    const filterFn = (menuItem: MenuItem) => fuzzySearch(menuItem.el.innerText);
    this.menuItems.forEach(item => item.filterMenuItems(filterValue, filterFn));
  }
}

export let sideMenu: SideMenu;

init.push(() => (sideMenu = SideMenu.create()));
