import { BaseEvent, clickOutside, EventHandler, withEventPrefix } from '@lpx/core';

export class ContextMenuCloseEvent extends BaseEvent<void> {
  static eventName = withEventPrefix('ctx-close');

  constructor(eventInit?: EventInit) {
    super(ContextMenuCloseEvent.eventName, null, eventInit);
  }

  static on(element: Element, handler: (event: CustomEvent<void>) => void) {
    BaseEvent.onEvent<void>(element, ContextMenuCloseEvent.eventName, handler);
  }
}

export interface ContextMenuToggleEventDetail {
  isOpened: boolean;
}

export class ContextMenuToggleEvent extends BaseEvent<ContextMenuToggleEventDetail> {
  static eventName = withEventPrefix('ctx-toggle');

  constructor(isOpened: boolean, eventInit?: EventInit) {
    super(ContextMenuToggleEvent.eventName, { isOpened }, eventInit);
  }

  static on(element: Element, handler: (event: CustomEvent<ContextMenuToggleEventDetail>) => void) {
    BaseEvent.onEvent<ContextMenuToggleEventDetail>(element, ContextMenuToggleEvent.eventName, handler);
  }
}

export class ContextMenuClose {
  private constructor(public readonly el: Element, private ctx: ContextMenu) {
    this.addClickListener();
  }

  static create(ctx: ContextMenu) {
    const el = ctx.getElement().querySelector('[data-lpx-close]');
    return el && new ContextMenuClose(el, ctx);
  }

  addClickListener() {
    EventHandler.on(this.el, 'click', () => {
      this.ctx.close();
      EventHandler.dispatch(this.el, new ContextMenuCloseEvent());
    });
  }
}

export class ContextMenuToggle {
  private constructor(public readonly el: Element, private ctx: ContextMenu) {
    this.addClickListener();
  }

  static createAll(ctx: ContextMenu) {
    return Array.from(document.querySelectorAll(`[data-lpx-ctx-toggle="${ctx.getName()}"]`)).map(
      el => new ContextMenuToggle(el, ctx)
    );
  }

  addClickListener() {
    EventHandler.on(this.el, 'click', () => {
      this.ctx.toggle();
      EventHandler.dispatch(this.el, new ContextMenuToggleEvent(this.ctx.isOpened()));
    });
  }
}

export class ContextMenu {
  classList = {
    show: 'show',
  };

  private constructor(private el: Element, private parentEl: Element, private name: string) {
    let exceptions = [ContextMenuClose.create(this)?.el];
    exceptions = exceptions.concat(ContextMenuToggle.createAll(this).map(ctx => ctx.el));
    clickOutside(this.parentEl, () => this.close(), exceptions);
  }

  getElement() {
    return this.el;
  }

  getName() {
    return this.name;
  }

  static create(el: Element, name: string) {
    const ctx = el.querySelector(`[data-lpx-context-menu=${name}]`);
    return ctx && new ContextMenu(ctx, el, name);
  }

  open() {
    this.el?.classList.add(this.classList.show);
  }

  isOpened() {
    return this.el?.classList.contains(this.classList.show);
  }

  close() {
    this.el?.classList.remove(this.classList.show);
  }

  toggle() {
    return this.isOpened() ? this.close() : this.open();
  }
}

export function createContextMenuWithin(el: Element, contextMenuName: string) {
  return ContextMenu.create(el, contextMenuName);
}
