import { withEventPrefix } from '../config';
import { BaseEvent } from '../events/base-event';
import { EventHandler } from '../utils/event-handler';
import { Caret } from './caret';

const MENU_ITEM_SELECTOR = '.lpx-menu-item';
const INNER_MENU_ITEM_SELECTOR = '.lpx-inner-menu-item';
const INNER_MENU_CLASS = 'lpx-inner-menu';
const COLLAPSED_CLASS = 'collapsed';
const SELECTED_CLASS = 'selected';

export class MenuItemToggleEvent extends BaseEvent<MenuItem> {
  static eventName = withEventPrefix('menu-item-toggle');

  constructor(item: MenuItem, eventInit?: EventInit) {
    super(MenuItemToggleEvent.eventName, item, eventInit);
  }

  static on(element: Element, handler: (event: CustomEvent<MenuItem>) => void) {
    BaseEvent.onEvent<MenuItem>(element, MenuItemToggleEvent.eventName, handler);
  }
}

export class MenuItemClickEvent extends BaseEvent<MenuItem> {
  static eventName = withEventPrefix('menu-item-click');

  constructor(item: MenuItem, eventInit?: EventInit) {
    super(MenuItemClickEvent.eventName, item, eventInit);
  }

  static on(element: Element, handler: (event: CustomEvent<MenuItem>) => void) {
    BaseEvent.onEvent<MenuItem>(element, MenuItemClickEvent.eventName, handler);
  }
}

export class MenuItem {
  childrenContainer: Element | null;
  caret: Caret;
  children: MenuItem[] = [];

  isInnerMenuExpanded: boolean;

  constructor(protected _el: Element) {
    this.initChildren();
    this.listenToClicks();
    this.initCaret();
  }

  static create(el: Element): MenuItem {
    return new MenuItem(el);
  }

  get el() {
    return this._el as HTMLElement;
  }

  public withEventHandler(callback: (event: CustomEvent<MenuItem>) => void) {
    // TODO: we should not need to access parentElement here
    MenuItemClickEvent.on(this._el.parentElement, callback);
    return this;
  }

  setInnerMenuExpanded() {
    this.showInnerMenu(() => (this.isInnerMenuExpanded = true));
  }

  setInnerMenuCollapsed() {
    this.hideInnerMenu(() => (this.isInnerMenuExpanded = false));
  }

  showInnerMenu(callback = () => {}) {
    this.runOnChildrenContainer(innerMenu => {
      innerMenu.classList.remove(COLLAPSED_CLASS);
      this._el.classList.add(SELECTED_CLASS);
      this.caret.turnUp();
      callback();
    });
  }

  hideInnerMenu(callback = () => {}) {
    this.runOnChildrenContainer(innerMenu => {
      innerMenu.classList.add(COLLAPSED_CLASS);
      this._el.classList.remove(SELECTED_CLASS);
      this.caret.turnDown();
      callback();
    });
  }

  toggleInnerMenuFromState() {
    if (this.isInnerMenuExpanded) {
      this.showInnerMenu();
    } else {
      this.hideInnerMenu();
    }
  }

  setSelected(selected: boolean) {
    this._el.classList.toggle(SELECTED_CLASS, selected);
  }

  setSelectedRecursive(compareFn: (child: MenuItem) => boolean) {
    // run through children to see if any of them fits the criteria
    let result = this.children.reduce(
      (finalResult, child) => finalResult || child.setSelectedRecursive(compareFn),
      false
    );

    /**
     * if any of the children fit the criteria or we are the one that fits the criteria, we are selected
     * and we should expand inner menu
     */
    if (result || compareFn(this)) {
      this.setSelected(true);
      this.setInnerMenuExpanded();
      result = true;
    }

    return result;
  }

  setVisible(visible: boolean) {
    this._el.classList.toggle('d-none', !visible);
  }

  toggleInnerMenuForFilter(filterValue: string, visible: boolean) {
    if (filterValue) {
      if (visible) {
        this.showInnerMenu();
      } else {
        this.hideInnerMenu();
      }
    } else {
      this.toggleInnerMenuFromState();
    }
  }

  filterMenuItems(filterValue: string, filterFn: (item: MenuItem) => boolean) {
    const result =
      this.children.reduce(
        (finalResult, child) => child.filterMenuItems(filterValue, filterFn) || finalResult,
        false
      ) || filterFn(this);
    this.setVisible(result);
    this.toggleInnerMenuForFilter(filterValue, result);
    return result;
  }

  selectChild(compareFn: (child: MenuItem) => boolean) {
    this.children.forEach(child => {
      child.setSelected(compareFn(child));
    });
  }

  toggleInnerMenu() {
    return this.isInnerMenuCollapsed() ? this.setInnerMenuExpanded() : this.setInnerMenuCollapsed();
  }

  isInnerMenuCollapsed() {
    return this.childrenContainer?.classList.contains(COLLAPSED_CLASS);
  }

  initChildren() {
    this.childrenContainer = Array.from(this._el.parentElement.children).find(child =>
      child.classList.contains(INNER_MENU_CLASS)
    );

    this.runOnChildrenContainer(innerMenu => {
      Array.from(innerMenu.querySelectorAll(`${INNER_MENU_ITEM_SELECTOR} > ${MENU_ITEM_SELECTOR}`)).forEach(child => {
        this.children.push(MenuItem.create(child).withEventHandler(this.onChildClick.bind(this)));
      });
    });

    return this;
  }

  onChildClick(event: CustomEvent<MenuItem>) {
    const clickedMenu = event.detail;
    this.children.forEach(child => {
      child.setSelected(child === clickedMenu);
    });
  }

  initCaret() {
    this.caret = Caret.create(this._el);
  }

  listenToClicks() {
    EventHandler.on(this._el, 'click', this.handleClick.bind(this));
    return this;
  }

  private handleClick() {
    if (this.childrenContainer) {
      this.toggleInnerMenu();
      EventHandler.dispatch(this._el, new MenuItemToggleEvent(this));
    } else {
      EventHandler.dispatch(this._el, new MenuItemClickEvent(this));
    }
  }

  private runOnChildrenContainer(fn: (el: Element) => void) {
    if (this.childrenContainer) {
      fn(this.childrenContainer);
    }
  }
}

