import {
  addDefaultSettings,
  BaseEvent,
  EventHandler,
  init,
  setCookieAndLocalStorage,
  SettingEvent,
  withEventPrefix,
} from '@lpx/core';
import { listenForThemeChanges, readSystemTheme } from '../utils';

export const APPEARANCE_SETTING_NAME = 'appearance';
export const APPEARANCE_SYSTEM = 'system';

addDefaultSettings({
  [APPEARANCE_SETTING_NAME]: APPEARANCE_SYSTEM,
});

export interface AppearanceSetting {
  theme: string;
}

export class AppearanceSettingEvent extends SettingEvent<AppearanceSetting> {
  static eventName = withEventPrefix('appearance-setting');
  constructor(theme: string) {
    super(AppearanceSettingEvent.eventName, { theme });
  }

  static on(element: Element, handler: (event: CustomEvent<AppearanceSetting>) => void) {
    BaseEvent.onEvent<AppearanceSetting>(element, AppearanceSettingEvent.eventName, handler);
  }
}

export class AppearenceSettingService {
  loadedTheme: string;
  chosenTheme: string;

  private static _instance: AppearenceSettingService;

  private constructor() {}

  static init() {
    if (!AppearenceSettingService._instance) {
      AppearenceSettingService._instance = new AppearenceSettingService().withSystemListener().withEventListener();
    }

    return AppearenceSettingService._instance;
  }

  static get instance() {
    return AppearenceSettingService._instance;
  }

  changeTheme(setting: AppearanceSetting) {
    let cssToLoad = setting.theme;
    this.chosenTheme = setting.theme;
    if (setting.theme === APPEARANCE_SYSTEM) {
      cssToLoad = readSystemTheme();
    }
    CSSLoadEvent.dispatch({ previousTheme: this.loadedTheme, theme: cssToLoad });
    this.loadedTheme = cssToLoad;
  }

  withSystemListener(shouldListenToSystem = true) {
    if (shouldListenToSystem) {
      listenForThemeChanges(
        () => this.chosenTheme === APPEARANCE_SYSTEM && this.changeTheme({ theme: APPEARANCE_SYSTEM })
      );
    }

    return this;
  }

  withEventListener() {
    AppearanceSettingEvent.on(document.body, event => this.changeTheme(event.detail));

    return this;
  }
}

init.push(AppearenceSettingService.init);

export interface CSSLoadDetail {
  previousTheme: string;
  theme: string;
}

export class CSSLoadEvent extends BaseEvent<CSSLoadDetail> {
  static eventName = withEventPrefix('css-load');

  constructor(detail: CSSLoadDetail, eventInit?: EventInit) {
    super(CSSLoadEvent.eventName, detail, eventInit);
  }

  static on(handler: (event: CustomEvent<AppearanceSetting>) => void) {
    BaseEvent.onEvent(document.body, CSSLoadEvent.eventName, handler);
  }

  static create(detail: CSSLoadDetail, eventInit?: EventInit) {
    return new CSSLoadEvent(detail, eventInit);
  }

  static dispatch(detail: CSSLoadDetail, eventInit?: EventInit) {
    if (EventHandler.dispatch(document.body, CSSLoadEvent.create(detail, eventInit))) {
      setCookieAndLocalStorage('loaded-css', detail.theme);
      this.updateBodyClass(detail);
    }
  }

  static updateBodyClass(detail: CSSLoadDetail) {
    const previousTheme = detail.previousTheme;
    const theme = detail.theme;

    if (previousTheme) {
      document.body.classList.remove(`lpx-theme-${previousTheme}`);
    }

    document.body.classList.add(`lpx-theme-${theme}`);
  }
}
