import { withCookiePrefix, withLocalStoragePrefix } from '../config';

export function cookieResolver(cookieName: string): string {
  const cookie = document.cookie.split(';').find(c => c.trim().startsWith(`${withCookiePrefix(cookieName)}=`));
  if (!cookie) {
    return '';
  }
  return cookie.split('=')[1];
}

export function localStorageResolver(key: string): string {
  return localStorage.getItem(withLocalStoragePrefix(key));
}

export function setCookie(name: string, value: string, days: number = 365): void {
  const expires = new Date();
  expires.setTime(expires.getTime() + days * 24 * 60 * 60 * 1000);
  document.cookie = `${withCookiePrefix(name)}=${value};expires=${expires.toUTCString()};path=/`;
}

export function setLocalStorage(key: string, value: string): void {
  localStorage.setItem(withLocalStoragePrefix(key), value);
}

export function setCookieAndLocalStorage(name: string, value: string, days: number = 365): void {
  setCookie(name, value, days);
  setLocalStorage(name, value);
}

export function readFromCookieOrLocalStorage(name: string): string {
  return cookieResolver(name) || localStorageResolver(name);
}

export function readFromLocalStorageOrCookie(name: string): string {
  return localStorageResolver(name) || cookieResolver(name);
}
