import bootstrap from 'bootstrap';
import { init } from './init';

init.push(() => {
  initPopovers();
  initTooltips();
});

function getElements(toggle) {
  return [].slice.call(document.querySelectorAll(`[data-bs-toggle=${toggle}]`));
}

function initPopovers() {
  const elemList = getElements('popover');
  elemList.forEach(function (popoverTriggerEl) {
    new bootstrap.Popover(popoverTriggerEl);
  });
}

function initTooltips() {
  const elemList = getElements('tooltip');
  elemList.forEach(function (popoverTriggerEl) {
    new bootstrap.Tooltip(popoverTriggerEl);
  });
}
