import { init } from '@lpx/core';
import PerfectScrollbar from 'perfect-scrollbar';

init.push(() => {
  const psElementSelectors = ['.lpx-scroll-container'];
  psElementSelectors.forEach(selector => {
    const elements = document.querySelectorAll(selector);
    elements.forEach(e => new PerfectScrollbar(e, { suppressScrollX: true }));
  });
});
