import { clickOutside, EventHandler } from '../utils';
import { Caret } from './caret';

const SELECTOR_DATA_TOGGLE = '[data-lpx-toggle="breadcrumb"]';
const BREADCRUMB_ITEM_SELECTOR = '.lpx-breadcrumb-item';
const EXPANDED_CLASS = 'expanded';

export class Breadcrumb {
  caret: Caret;
  private constructor(private el: HTMLElement) {
    this.addListener();
    this.caret = Caret.create(this.findParent());
  }

  static create(el: HTMLElement) {
    return new Breadcrumb(el);
  }

  isShown() {
    return this.findParent()?.classList.contains(EXPANDED_CLASS);
  }

  show() {
    this.findParent()?.classList.add(EXPANDED_CLASS);
    this.caret.turnUp();
  }

  hide() {
    this.findParent()?.classList.remove(EXPANDED_CLASS);
    this.caret.turnDown();
  }

  addListener() {
    EventHandler.on(this.el, 'click', this.toggleDropdown.bind(this));
    clickOutside(this.el, this.hide.bind(this));
  }

  findParent(): Element | null {
    return this.el.closest(BREADCRUMB_ITEM_SELECTOR);
  }

  toggleDropdown() {
    return this.isShown() ? this.hide() : this.show();
  }
}

document.querySelectorAll(SELECTOR_DATA_TOGGLE).forEach(Breadcrumb.create);
