export function readSystemTheme() {
  return matchMediaDark().matches ? 'dark' : 'light';
}

export function listenForThemeChanges(handler: (theme: string) => void) {
  // TODO: use EventHandler
  matchMediaDark().addEventListener('change', event => {
    handler(event.matches ? 'dark' : 'light');
  });
}

export function matchMediaDark() {
  return window.matchMedia('(prefers-color-scheme: dark)');
}
