import { BaseEvent } from './base-event';

export abstract class SettingEvent<T> extends BaseEvent<T> {
  constructor(type: string, data: T, eventInit?: EventInit) {
    super(type, data, eventInit);
  }

  static create<T extends SettingEvent<any>>(type: new (data: any) => T, data: any) {
    return new type(data);
  }
}
