import { EventHandler } from '../utils/event-handler';

export abstract class BaseEvent<T> {
  constructor(public type: string, public detail?: T, public eventInit?: EventInit) {}

  protected static onEvent<Type>(element: Element, type: string, handler: (event: CustomEvent<Type>) => void) {
    EventHandler.onCustomEvent<Type>(element, type, handler);
  }
}
