export function replaceStyleWith(path: string, id: string, previousId: string) {
  const link = document.createElement('link');
  link.href = path;
  link.type = 'text/css';
  link.rel = 'stylesheet';
  link.id = id;

  document.getElementsByTagName('head')[0].appendChild(link);
  link.onload = () => {
    if (previousId) {
      document.querySelector(`#${previousId}`)?.remove();
    }
  };
  return link;
}
