import { EventHandler } from './event-handler';

export function clickOutside(el: Element, callback: () => void, exceptions: Array<Element> = []) {
  EventHandler.onDocument('click', (event: MouseEvent) => {
    if (
      !el.contains(event.target as Element) &&
      exceptions.filter(Boolean).every(ex => !ex.contains(event.target as Element))
    ) {
      callback();
    }
  });
}
