export const CARET_SELECTOR = '.lpx-caret';
export const CARET_CSS_CLASS = 'lpx-caret';
export const CARET_UP_CSS_CLASS = 'bi-chevron-up';
export const CARET_DOWN_CSS_CLASS = 'bi-chevron-down';

export class Caret {
  el: Element;
  constructor(parentEl: Element) {
    this.el = parentEl.querySelector(CARET_SELECTOR);
  }

  static create(parentEl: Element) {
    return new Caret(parentEl);
  }

  isUp() {
    return this.el?.classList.contains(CARET_UP_CSS_CLASS);
  }

  isDown() {
    return this.el?.classList.contains(CARET_DOWN_CSS_CLASS);
  }

  turnUp() {
    this.run(() => {
      this.el.classList.remove(CARET_DOWN_CSS_CLASS);
      this.el.classList.add(CARET_UP_CSS_CLASS);
    });
  }

  turnDown() {
    this.run(() => {
      this.el.classList.remove(CARET_UP_CSS_CLASS);
      this.el.classList.add(CARET_DOWN_CSS_CLASS);
    });
  }

  toggle() {
    this.run(() => {
      if (this.isUp()) {
        this.turnDown();
      } else {
        this.turnUp();
      }
    });
  }

  run(fn: () => void) {
    if (this.el) {
      fn();
    }
  }
}
