import { withEventPrefix } from '../config';
import { BaseEvent } from './base-event';
import { SettingEvent } from './setting-event';

export interface LanguageEvent {
  language: string;
}

export class LanguageSettingEvent extends SettingEvent<LanguageEvent> {
  static eventName = withEventPrefix('language');

  constructor(language: string) {
    super(LanguageSettingEvent.eventName, { language });
  }

  static on(handler: (event: CustomEvent<LanguageEvent>) => void) {
    BaseEvent.onEvent(document.body, LanguageSettingEvent.eventName, handler);
  }
}
