import { EventHandler, init, MenuItem, setCookieAndLocalStorage, SettingEvent } from '@lpx/core';
import { AppearanceSettingEvent } from '..';

const MOBILE_NAVBAR_SELECTOR = '.lpx-mobile-navbar-container';
const MOBILE_MENU_SELECTOR = '.lpx-mobile-menu';
const MOBILE_MENU_TOGGLE_ATTRIBUTE = 'data-lpx-mobile-menu-toggle';
const MOBILE_MENU_TOGGLE_SELECTOR = `[${MOBILE_MENU_TOGGLE_ATTRIBUTE}]`;
const MOBILE_MENU_TOGGLE_OPEN_CLASS = 'lpx-mobile-menu-toggle-open';
const MOBILE_MENU_ATTRIBUTE = 'data-lpx-mobile-menu';
const MOBILE_MENU_ATTRIBUTE_SELECTOR = `[${MOBILE_MENU_ATTRIBUTE}]`;
const DNONE_CLASS = 'd-none';
const HIDDEN_CLASS = 'hidden';
class MobileMenuToggle {
  target: string;
  private constructor(public readonly el: Element) {
    this.target = el.getAttribute(MOBILE_MENU_TOGGLE_ATTRIBUTE) || '';
  }

  static create(el: Element) {
    return new MobileMenuToggle(el);
  }

  withClickHandler(handler: (toggle: MobileMenuToggle) => void) {
    this.el.addEventListener('click', () => {
      this.toggle();
      handler(this);
    });
    return this;
  }

  isOpen() {
    return this.el.classList.contains(MOBILE_MENU_TOGGLE_OPEN_CLASS);
  }

  toggle() {
    this.el.classList.toggle(MOBILE_MENU_TOGGLE_OPEN_CLASS);
  }

  close() {
    this.el.classList.remove(MOBILE_MENU_TOGGLE_OPEN_CLASS);
  }

  add() {
    this.el.classList.add(MOBILE_MENU_TOGGLE_OPEN_CLASS);
  }
}

class InnerMobileMenu {
  menuItems: MenuItem[] = [];
  private constructor(public readonly el: Element) {}

  static create(el: Element) {
    return new InnerMobileMenu(el).withMenuItems();
  }

  toggle() {
    return this.isVisible() ? this.hide() : this.show();
  }

  show() {
    this.el.classList.remove(DNONE_CLASS);
  }

  hide() {
    this.el.classList.add(DNONE_CLASS);
  }

  isVisible() {
    return !this.el.classList.contains(DNONE_CLASS);
  }

  withMenuItems() {
    this.menuItems = Array.from(this.el.querySelectorAll('.outer-menu-item > .lpx-menu-item')).map(MenuItem.create);
    return this;
  }
}

class MobileSettingsMenu {
  private constructor(private menu: InnerMobileMenu) {}

  static create(menu: InnerMobileMenu): MobileSettingsMenu {
    return new MobileSettingsMenu(menu).withMenuListener().withEventListener();
  }

  withMenuListener() {
    this.menu.menuItems.forEach(item => item.withEventHandler(event => this.onMenuItemClick(item, event)));
    return this;
  }

  withEventListener() {
    // TODO: magic string
    const appearanceSetting = this.menu.menuItems.find(item => item.el.dataset.lpxSettingGroup === 'appearance');
    AppearanceSettingEvent.on(document.body, event =>
      appearanceSetting?.selectChild(child => child.el.dataset.lpxSetting === event.detail.theme)
    );

    return this;
  }

  onMenuItemClick(group: MenuItem, event: CustomEvent<MenuItem>) {
    const detail = event.detail.el as HTMLElement;
    const name = group.el.dataset.lpxSettingGroup;
    const selectedSetting = detail.dataset.lpxSetting;
    const settingEvent = SettingEvent.create(AppearanceSettingEvent, selectedSetting);
    EventHandler.dispatch(event.target as Element, settingEvent);
    this.persistSelected(name, selectedSetting);
  }

  persistSelected(name: string, setting: string) {
    setCookieAndLocalStorage(name, setting);
  }
}

class MobileNavbar {
  mobileMenu: Element;
  innerMobileMenus = new Map<string, InnerMobileMenu>();

  settings: MobileSettingsMenu;

  toggles: MobileMenuToggle[] = [];

  private constructor(public readonly el: Element) {}

  static create(el: Element) {
    return new MobileNavbar(el).withMobileMenu();
  }

  withMobileMenu() {
    this.mobileMenu = this.el.querySelector(MOBILE_MENU_SELECTOR);
    if (!this.mobileMenu) {
      console.warn('Mobile menu not found');
      return this;
    }

    this.toggles = Array.from(this.el.querySelectorAll(MOBILE_MENU_TOGGLE_SELECTOR)).map(el =>
      MobileMenuToggle.create(el).withClickHandler(this.onToggleClick.bind(this))
    );

    this.el.querySelectorAll(MOBILE_MENU_ATTRIBUTE_SELECTOR).forEach(el => {
      const innerMobileMenu = InnerMobileMenu.create(el);
      const name = el.getAttribute(MOBILE_MENU_ATTRIBUTE);
      this.innerMobileMenus.set(name, innerMobileMenu);

      // TODO: magic string
      if (name === 'settings') {
        this.settings = MobileSettingsMenu.create(innerMobileMenu);
      }
    });

    return this;
  }

  onToggleClick(toggle: MobileMenuToggle) {
    const shouldOpen = toggle.isOpen();

    if (shouldOpen) {
      this.innerMobileMenus.forEach((menu, name) => {
        if (name === toggle.target) {
          menu.show();
        } else {
          menu.hide();
        }
      });
      this.toggles.filter(t => t !== toggle).forEach(t => t.close());
      this.mobileMenu.classList.remove(HIDDEN_CLASS);
    } else {
      this.mobileMenu.classList.add(HIDDEN_CLASS);
    }
  }
}

export let mobileNavbar: MobileNavbar;

init.push(() => {
  document.querySelectorAll(MOBILE_NAVBAR_SELECTOR).forEach(el => (mobileNavbar = MobileNavbar.create(el)));
});
