export class LpxToggle {
  constructor() {
    document.addEventListener('DOMContentLoaded', () => {
      const toggleButtons = document.querySelectorAll('.lpx-toggle');
      toggleButtons.forEach((button: HTMLElement) => {
        button.addEventListener('click', () => {
          Array.from(button.parentElement.children)
            .filter(elem => elem !== button)
            .forEach((sibling: HTMLElement) => {
              const target = document.querySelector(`#${sibling.dataset.lpxToggle}`);
              if (target) {
                target.classList.add('d-none');
              }
            });
          const target = document.querySelector(`#${button.dataset.lpxToggle}`);
          if (target) {
            target.classList.toggle('d-none');
          }
        });
      });
    });
  }
}
export const lpxToggle = new LpxToggle();
