import { BaseEvent } from '../events/base-event';

export class EventHandler {
  static on<T extends Event>(element: Element | null | undefined, type: string, handler: (event: T) => void) {
    if (element) {
      element.addEventListener(type, handler);
    }
  }

  static onCustomEvent<T>(element: Element, type: string, handler: (event: CustomEvent<T>) => void) {
    element.addEventListener(type, (event: CustomEvent<T>) => {
      if (!event.defaultPrevented) {
        handler(event);
      }
    });
  }

  static onDocument(type: string, handler: (event: Event) => void) {
    document.addEventListener(type, handler);
  }

  static onWindow(type: string, handler: (event: Event) => void) {
    window.addEventListener(type, handler);
  }

  static listen<T extends { addEventListener: any }>(el: T, type: string, handler: (event: Event) => void) {
    el.addEventListener(type, handler);
  }

  static dispatch(el: Element, event: BaseEvent<any>) {
    return el.dispatchEvent(
      new CustomEvent(event.type, { bubbles: true, cancelable: true, ...event.eventInit, detail: event.detail })
    );
  }
}
