import {
  addDefaultSettings,
  BaseEvent,
  EventHandler,
  init,
  LanguageSettingEvent,
  SettingEvent,
  withEventPrefix,
} from '@lpx/core';
import {
  AppearanceSettingEvent,
  APPEARANCE_SETTING_NAME,
  AppearenceSettingService,
  ContextMenu,
  ContextMenuCloseEvent,
  ContextMenuToggleEvent,
  createContextMenuWithin,
  SettingGroup,
} from '@lpx/pro';

const SETTINGS_SELECTOR = '#lpx-settings';
export abstract class SideMenuSettingGroup extends SettingGroup {
  protected iconOnSettings: Element;
  protected iconList: { [key: string]: string } = {};

  protected constructor(protected setting: Settings, name: string, eventType: new (data: any) => SettingEvent<any>) {
    super(name, eventType);
    this.createSettingGroupWithMenu(setting.querySelector(`[data-lpx-setting-group="${name}"]`));
    this.initIcon();
  }

  protected initIcon() {
    this.createSettingIconListener();
    this.buildIconList();
    return this;
  }

  createSettingIconListener() {
    const icon = this.setting.querySelector(`[data-lpx-setting-icon="${this.name}"]`);
    if (icon) {
      EventHandler.on(icon, 'click', this.onIconClick.bind(this));
      this.iconOnSettings = icon;
    }

    return this;
  }

  buildIconList() {
    // TODO: this can be handled by menuItem
    this.menuItem.children.forEach(item => {
      const key = item.el.dataset.lpxSetting;
      const icon = item.el.querySelector('.lpx-icon');
      if (icon) {
        icon.classList.forEach(className => {
          if (className.startsWith('bi-')) {
            this.iconList[key] = className;
          }
        });
      }
    });

    return this;
  }

  updateSelected(selectedSetting: string) {
    const icon = this.iconOnSettings.querySelector('.bi');
    if (icon) {
      icon.classList.forEach(className => {
        if (className.startsWith('bi-')) {
          icon.classList.remove(className);
        }
      });
      icon.classList.add(this.iconList[selectedSetting]);
    }
  }

  onIconClick() {
    this.expand();
    this.setting.toggleGroup(this);
  }
}

export class AppearanceSettingGroup extends SideMenuSettingGroup {
  private constructor(setting: Settings) {
    super(setting, APPEARANCE_SETTING_NAME, AppearanceSettingEvent);
  }

  static create(setting: Settings) {
    return new AppearanceSettingGroup(setting).withPersistedValue().withEventListener();
  }

  withEventListener() {
    AppearanceSettingEvent.on(document.body, event => this.updateAllSelected(event.detail.theme));
    return this;
  }

  setInitialDefaultValue(defaultValue: string) {
    AppearenceSettingService.instance.changeTheme({ theme: defaultValue });
  }
}

export const enum ContainerWidthType {
  boxed = 'boxed',
  full = 'full',
}

export interface ContainerWidthSetting {
  type: ContainerWidthType;
}

export class ContainerWidthSettingEvent extends SettingEvent<ContainerWidthSetting> {
  static eventName = withEventPrefix('container-width');

  constructor(type: ContainerWidthType, eventInit?: EventInit) {
    super(ContainerWidthSettingEvent.eventName, { type }, eventInit);
  }

  static on(element: Element, handler: (event: CustomEvent<ContainerWidthSetting>) => void) {
    BaseEvent.onEvent<ContainerWidthSetting>(element, ContainerWidthSettingEvent.eventName, handler);
  }
}

const CONTAINER_WIDTH_SETTING_NAME = 'containerWidth';

addDefaultSettings({
  [CONTAINER_WIDTH_SETTING_NAME]: ContainerWidthType.boxed,
});

export class ContainerWidthSettingGroup extends SideMenuSettingGroup {
  containerWidthClasses = [ContainerWidthType.boxed, ContainerWidthType.full];

  private constructor(setting: Settings) {
    super(setting, CONTAINER_WIDTH_SETTING_NAME, ContainerWidthSettingEvent);
  }

  static create(setting: Settings) {
    return new ContainerWidthSettingGroup(setting).withEventHandler().withPersistedValue();
  }

  withEventHandler() {
    ContainerWidthSettingEvent.on(document.body, event => this.changeContainerWidth(event.detail));
    return this;
  }
  changeContainerWidth(detail: ContainerWidthSetting) {
    const container = document.querySelector<HTMLElement>('#lpx-wrapper');
    if (container) {
      container.classList.remove(...this.containerWidthClasses);
      container.classList.add(detail.type);
    }
  }

  setInitialDefaultValue(defaultValue: string) {
    this.changeContainerWidth({ type: defaultValue as ContainerWidthType });
  }
}

const LANGUAGE_SETTING_NAME = 'language';

export class LanguageSettingGroup extends SideMenuSettingGroup {
  private constructor(setting: Settings) {
    super(setting, LANGUAGE_SETTING_NAME, LanguageSettingEvent);
  }

  static create(setting: Settings) {
    return new LanguageSettingGroup(setting).withPersistedValue();
  }

  setInitialDefaultValue(defaultValue: string) {
    // TODO: set default language
  }
}

export class Settings {
  contextMenuName = 'settings-context-menu';

  contextMenu: ContextMenu;

  settingGroups: SideMenuSettingGroup[] = [];

  private constructor(private _el: HTMLElement) {}

  get el() {
    return this._el;
  }

  querySelector(selector: string) {
    return this.el.querySelector(selector);
  }

  static create(el: HTMLElement) {
    return new Settings(el).createContextMenu().createSettingGroups().createSettingIconListener();
  }

  createContextMenu() {
    this.contextMenu = createContextMenuWithin(this._el, this.contextMenuName);
    return this;
  }

  createSettingGroups() {
    this.settingGroups.push(AppearanceSettingGroup.create(this));
    this.settingGroups.push(ContainerWidthSettingGroup.create(this));
    this.settingGroups.push(LanguageSettingGroup.create(this));
    return this;
  }

  createSettingIconListener() {
    ContextMenuToggleEvent.on(this.el, event => this.openContextMenuWithAll(event.detail.isOpened));
    ContextMenuCloseEvent.on(this.el, this.closeSettingGroupBut.bind(this));
    return this;
  }

  openContextMenuWithAll(isOpened: boolean) {
    if (isOpened) {
      this.settingGroups.forEach(g => g.expand());
    }
  }

  toggleGroup(group: SideMenuSettingGroup) {
    this.closeSettingGroupBut(group);
    this.contextMenu.open();
  }

  closeSettingGroupBut(group: SideMenuSettingGroup) {
    this.settingGroups.filter(g => g !== group).forEach(gr => gr.collapse());
  }
}

init.push(() => {
  document.querySelectorAll(SETTINGS_SELECTOR).forEach(Settings.create);
});
